import http from "../plugins/axios";

/**
 *
 * @param params
 * @returns {Promise<any>}
 */
async function getOrders({ filter, paginate }) {
  const response = await http.post("/orders", null, {
    params: { ...filter, ...paginate }
  });
  return response.data;
}

/**
 *
 * @param id
 * @returns {Promise<any>}
 */
async function getOrder(id) {
  const response = await http.post(`/orders/${id}`);
  return response.data;
}

/**
 *
 * @param id
 * @param data
 * @returns {Promise<void>}
 */
async function updateOrder(id, data) {
  await http.post(`/orders/${id}/update`, data);
}

export { getOrders, getOrder, updateOrder };
