<template>
  <v-app>
    <template v-if="auth">
      <Navbar />
      <NavigationDrawers />
      <ProfileDialog :profile="client" />
    </template>

    <template v-else>
      <GuestNavbar />
    </template>

    <v-main :class="{ app__main: auth }">
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
import store from "./store";
import { mapGetters } from "vuex";
import Navbar from "./components/Navbars/Navbar";
import GuestNavbar from "./components/Navbars/GuestNavbar";
import NavigationDrawers from "./components/NavigationDrawers";
import ProfileDialog from "./components/Dialogs/ProfileDialog";

export default {
  name: "App",
  data: () => ({
    loaderOnRefreshPage: true
  }),
  computed: {
    ...mapGetters({
      auth: "getAuth",
      client: "getWorker"
    })
  },
  components: {
    Navbar,
    GuestNavbar,
    NavigationDrawers,
    ProfileDialog
  },
  async beforeMount() {
    // TODO: Authorization
    await store.dispatch("fetchClientInfo");
    if (store.getters.getAuth) await this.$router.push({ name: "Orders" });
  }
};
</script>

<style scoped>
.app__main {
  padding: 60px 0 0 57px !important;
}
</style>
