<template>
  <v-dialog v-model="$store.state.dialogs.dialogs.profile.update" width="600">
    <v-card>
      <v-card-title>
        <span class="text-h5">Информация о профиле</span>
      </v-card-title>

      <v-card-text>
        <v-container>
          <h2>Оповещения</h2>
          <v-row class="pb-3">
            <v-col cols="12" class="pb-3">
              <v-text-field
                v-model="email"
                :rules="emailRules"
                :label="
                  profile.email ? profile.email : 'Почтовый адрес отсуствует'
                "
                type="email"
              />
            </v-col>
            <v-col cols="12" class="pb-3">
              <v-btn
                :disabled="!isCorrectEmail"
                @click="updateEmailProfile"
                color="primary"
              >
                Изменить
              </v-btn>
            </v-col>
          </v-row>
          <hr />
          <h2 class="py-5">Безопасность</h2>
          <h3>Изменение пароля</h3>
          <v-row>
            <v-col cols="12">
              <v-text-field
                label="Старый пароль"
                v-model="passwordReset.password"
                type="password"
                placeholder="Старый пароль"
              />
            </v-col>
            <v-col cols="12" class="pt-0">
              <v-text-field
                label="Новый пароль"
                v-model="passwordReset.newPassword"
                type="password"
                placeholder="Новый пароль"
              />
            </v-col>
            <v-col cols="12" class="pt-0">
              <v-text-field
                label="Повторите пароль"
                v-model="passwordReset.newPasswordConfirm"
                type="password"
                placeholder="Повторите пароль"
              />
            </v-col>
            <v-col cols="12" class="pb-3">
              <v-btn
                :disabled="isEqualPasswords"
                @click="changePassword"
                color="primary"
              >
                Изменить
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "ProfileDialog",
  data() {
    return {
      email: "",
      emailRules: [
        v =>
          /[a-z0-9-_]@(mail|gmail|rambler|yandex).(com|ru)/.test(v) ||
          "Не корректный ввод почты"
      ],
      passwordReset: {
        password: "",
        newPassword: "",
        newPasswordConfirm: ""
      }
    };
  },
  methods: {
    ...mapActions({
      updatePassword: "fetchResetPassword",
      updateEmail: "fetchUpdateEmail",
      updateClientInfo: "fetchClientInfo"
    }),
    async updateEmailProfile() {
      if (!this.isCorrectEmail) {
        return;
      }

      await this.updateEmail({ email: this.email });
      await this.updateClientInfo();
      this.email = "";
    },
    async changePassword() {
      await this.updatePassword(this.passwordReset);
      Object.keys(this.passwordReset).forEach(
        key => (this.passwordReset[key] = "")
      );
      this.$toast("Пароль успешно изменен", {
        color: "green",
        x: "right",
        y: "top"
      });
    }
  },
  computed: {
    isCorrectEmail() {
      return /[a-z0-9-_]@(mail|gmail|rambler|yandex).(com|ru)/.test(this.email);
    },
    isEqualPasswords() {
      if (
        !this.passwordReset.newPassword.length ||
        !this.passwordReset.newPasswordConfirm ||
        !this.passwordReset.password.length
      ) {
        return true;
      }

      return (
        this.passwordReset.newPassword !== this.passwordReset.newPasswordConfirm
      );
    }
  },
  props: {
    profile: {
      type: Object,
      required: true
    }
  }
};
</script>

<style scoped></style>
