<template>
  <v-app-bar
    class="v-app-bar--fixed elevation-4"
    color="white lighten-4"
    flat
    dense
  >
    <v-app-bar-nav-icon></v-app-bar-nav-icon>

    <v-toolbar-title>
      <router-link
        :to="{ name: 'Orders' }"
        class="font-weight-bold text--secondary text-decoration-none"
      >
        {{ APP_NAME }} | Панель управления
      </router-link>
    </v-toolbar-title>

    <v-spacer></v-spacer>

    <v-menu>
      <template v-slot:activator="{ on: menu, attrs }">
        <v-btn icon v-bind="attrs" v-on="{ ...menu }" class="my-5">
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-list>
        <v-list-item @click.prevent="switchDialog" link>
          <v-list-item-icon class="mx-1">
            <v-icon>mdi-account</v-icon>
          </v-list-item-icon>
          <v-list-item-title>
            Профиль
          </v-list-item-title>
        </v-list-item>
        <v-list-item @click.prevent="logout" link>
          <v-list-item-icon class="mx-1">
            <v-icon>mdi-location-exit</v-icon>
          </v-list-item-icon>
          <v-list-item-title>
            Выход
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Navbar",
  computed: {
    APP_NAME() {
      return process.env.VUE_APP_NAME;
    }
  },
  methods: {
    ...mapActions({
      exit: "fetchLogout",
      handleDialog: "show"
    }),
    async switchDialog() {
      this.handleDialog("profile:update");
    },
    async logout() {
      await this.exit();
      await this.$router.push({ name: "Login" });
    }
  }
};
</script>

<style scoped></style>
