import { getOrders, getOrder, updateOrder } from "../services/orders.service";

export const state = () => ({
  ordersList: [],
  orderPaginate: {},
  currentOrder: null
});

export const actions = {
  async fetchOrders({ commit }, { filter, paginate: paginateData }) {
    const { orders, paginate } = await getOrders({
      filter,
      paginate: paginateData
    });
    commit("updateOrders", orders);
    commit("updateOrderPaginate", paginate);
  },
  async fetchOrder({ commit }, id) {
    const { order } = await getOrder(id);
    commit("updateOrder", order);
  },
  // eslint-disable-next-line no-unused-vars
  async fetchUpdateOrder({ commit, dispatch }, { id, form, notify }) {
    await updateOrder(id, { form, notify });
    await dispatch("fetchOrder", id);
  }
};

export const mutations = {
  updateOrders(state, orders) {
    state.ordersList = orders;
  },
  updateOrder(state, order) {
    state.currentOrder = order;
  },
  updateOrderInOrdersList(state) {
    const index = state.ordersList.findIndex(iterable => {
      return iterable.id === state.currentOrder.id;
    });

    state.ordersList[index] = { ...state.currentOrder };
  },
  updateOrderPaginate(state, paginate) {
    state.orderPaginate = paginate;
  }
};

export const getters = {
  getOrders(state) {
    return state.ordersList;
  },
  getOrder(state) {
    return state.currentOrder;
  },
  getOrderPaginate(state) {
    return state.orderPaginate;
  }
};
